body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  background-color: #fde767;
  border: 5px double #f3b95f;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f3b95f;
  border: 2px double #6895d2;
  border-radius: 50%;
  width: 5%;
  height: 5%;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stopwatch__hand {
  transform-origin: 50% 100%;
  animation-name: rotating;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch__hand--big-hand {
  z-index: 2;
  background-color: #6895d2;
  border-radius: 10px;
  width: 3%;
  height: 45%;
  animation-duration: 5s;
}

.stopwatch__hand--small-hand {
  z-index: 1;
  background-color: #d04848;
  border-radius: 10px;
  width: 4%;
  height: 30%;
  animation-duration: 60s;
}

@keyframes rotating {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

